"use client";

import React, { useState, useEffect, useRef } from "react";
import { INITIAL_PLAYER_DATA, PRESET_RESPONSES, CUSTOM_COMMANDS, PlayerData, SYSTEM_MESSAGES, CRIMES, LOCATIONS } from "@/lib/game-data";
import { gameSocket, CrimeEvent } from "@/lib/socket";

type Message = {
  role: string;
  content: string;
};

type OtherPlayer = {
  id: string;
  name: string;
  level: number;
  wanted_level: number;
};

const defaultPlayerData: PlayerData = {
  discord_id: "",
  level: 1,
  points: 0,
  xp: 0,
  role: "Street Runner",
  unique_ability: "Stealth Master",
  wanted_level: 0,
  inventory: []
};

export default function HomePage() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [showCrimeMenu, setShowCrimeMenu] = useState(false);
  const chatWindowRef = useRef<HTMLDivElement>(null);
  const [loading, setLoading] = useState(false);
  const [playerData, setPlayerData] = useState<PlayerData>(defaultPlayerData);
  const [otherPlayers, setOtherPlayers] = useState<OtherPlayer[]>([]);
  const [crimeEvents, setCrimeEvents] = useState<CrimeEvent[]>([]);
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    if (!isInitialized) {
      const initialData = {
        ...defaultPlayerData,
        discord_id: `player_${Math.random().toString(36).substring(7)}`,
        level: Math.floor(Math.random() * 5) + 1,
        points: Math.floor(Math.random() * 1000),
        xp: Math.floor(Math.random() * 500),
        role: ["Street Runner", "Netrunner", "Corporate Agent", "Tech Specialist"][Math.floor(Math.random() * 4)],
        unique_ability: ["Stealth Master", "Code Breaker", "Social Engineer", "Tech Savvy"][Math.floor(Math.random() * 4)],
        inventory: [
          "Neural Implant",
          "Encrypted Data Chip",
          "Plasma Pistol",
          "Stealth Cloak"
        ].slice(0, Math.floor(Math.random() * 4) + 1)
      };
      setPlayerData(initialData);
      setMessages([{ role: "system", content: SYSTEM_MESSAGES.WELCOME }]);
      setIsInitialized(true);
    }
  }, [isInitialized]);

  useEffect(() => {
    if (chatWindowRef.current) {
      chatWindowRef.current.scrollTop = chatWindowRef.current.scrollHeight;
    }
  }, [messages]);

  useEffect(() => {
    if (playerData.discord_id && isInitialized) {
      gameSocket.connect();

      const unsubscribe = gameSocket.subscribe((update) => {
        if (update.type === "player_update") {
          const updatedPlayer: OtherPlayer = update.data;
          setOtherPlayers((prev) => {
            const exists = prev.find((p) => p.id === updatedPlayer.id);
            if (exists) {
              return prev.map((p) => (p.id === updatedPlayer.id ? updatedPlayer : p));
            } else {
              return [...prev, updatedPlayer];
            }
          });
        } else if (update.type === "player_leave") {
          const leavingId: string = update.data.id;
          setOtherPlayers((prev) => prev.filter((p) => p.id !== leavingId));
        } else if (update.type === "chat_message") {
          const chatMsg = update.data;
          appendMessage("assistant", `${chatMsg.player.name}: ${chatMsg.message}`);
        } else if (update.type === "crime_event") {
          const crimeEvent: CrimeEvent = update.data;
          setCrimeEvents(prev => [crimeEvent, ...prev].slice(0, 5));
          
          if (crimeEvent.perpetrator === playerData.discord_id) {
            setPlayerData(prev => ({
              ...prev,
              wanted_level: Math.min(5, prev.wanted_level + crimeEvent.severity)
            }));
          }
        } else if (update.type === "wanted_level_update") {
          if (update.data.playerId === playerData.discord_id) {
            setPlayerData(prev => ({
              ...prev,
              wanted_level: update.data.wantedLevel
            }));
          }
        }
      });

      gameSocket.updatePlayer(playerData);

      return () => {
        unsubscribe();
      };
    }
  }, [playerData.discord_id, isInitialized]);

  function handleCrimeClick(crime: typeof CRIMES[0]) {
    gameSocket.commitCrime(crime.name, playerData);
    setShowCrimeMenu(false);
    appendMessage("system", `Attempting: ${crime.name}`);
  }

  async function handleCommand(command: string) {
    // Check for custom commands first
    const customCommand = CUSTOM_COMMANDS[command as keyof typeof CUSTOM_COMMANDS];
    if (customCommand) {
      if (typeof customCommand === 'function') {
        return customCommand(playerData);
      }
      return customCommand;
    }

    // Then check preset commands
    if (command === "!crime") {
      setShowCrimeMenu(true);
      return PRESET_RESPONSES[command]();
    }
    
    const response = PRESET_RESPONSES[command as keyof typeof PRESET_RESPONSES];
    if (typeof response === "function") {
      return response(playerData);
    }
    return response || "[ERR] Unknown command";
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!input.trim()) return;
    const userMessage = input.trim();
    appendMessage("user", userMessage);
    setInput("");
    setLoading(true);

    if (userMessage.startsWith("!") || userMessage.startsWith("-") || userMessage.length === 1) {
      const response = await handleCommand(userMessage.toLowerCase());
      appendMessage("system", response);
    } else {
      gameSocket.sendChatMessage(userMessage, playerData);
    }
    
    setLoading(false);
  }

  function appendMessage(role: string, content: string) {
    setMessages((prev) => [...prev, { role, content }]);
  }

  if (!isInitialized) {
    return <div className="flex items-center justify-center min-h-screen bg-[#0a0f1c] text-gray-200">Loading...</div>;
  }

  return (
    <div className="flex flex-col min-h-screen bg-[#0a0f1c] text-gray-200 font-['Fira_Mono']">
      <header className="bg-gray-800 p-6 flex justify-between items-center border-b-2 border-red-600 shadow-lg">
        <h1 className="text-3xl font-bold text-red-600 neon-text tracking-wider transform hover:scale-105 transition-transform">
          Sin City RPG
        </h1>
        <div className="bg-gray-900 px-4 py-2 rounded-lg border border-red-600/20">
          <span className="text-gray-400">Wanted Level: </span>
          <span className="text-red-500 font-bold">{playerData.wanted_level}</span>
          <div className="h-2 w-32 bg-gray-700 rounded-full mt-1">
            <div
              className="h-full bg-red-600 rounded-full transition-all duration-500"
              style={{ width: `${(playerData.wanted_level / 5) * 100}%` }}
            ></div>
          </div>
        </div>
      </header>
      <main className="flex flex-1 max-w-7xl mx-auto p-6 w-full gap-6">
        <div className="w-1/4 space-y-6">
          <div className="bg-gray-800/90 rounded-xl shadow-2xl p-6 backdrop-blur-sm border border-red-600/20">
            <h2 className="text-xl font-bold text-red-600 mb-4 border-b border-red-600/20 pb-2">
              Inventory
            </h2>
            <ul className="space-y-2">
              {playerData.inventory.length === 0 ? (
                <li className="text-gray-400 italic">No items yet.</li>
              ) : (
                playerData.inventory.map((item, index) => (
                  <li
                    key={index}
                    className="bg-gray-900/60 p-3 rounded-lg hover:bg-gray-900 transition-colors cursor-pointer flex items-center space-x-2"
                  >
                    <span className="w-2 h-2 bg-red-600 rounded-full"></span>
                    <span>{item}</span>
                  </li>
                ))
              )}
            </ul>
          </div>
          
          <div className="bg-gray-800/90 rounded-xl shadow-2xl p-6 backdrop-blur-sm border border-red-600/20">
            <h2 className="text-xl font-bold text-red-600 mb-4 border-b border-red-600/20 pb-2">
              Online Players
            </h2>
            <ul className="space-y-2">
              {otherPlayers.length === 0 ? (
                <li className="text-gray-400 italic">No other players online.</li>
              ) : (
                otherPlayers.map((player) => (
                  <li
                    key={player.id}
                    className="bg-gray-900/60 p-3 rounded-lg hover:bg-gray-900 transition-colors cursor-pointer"
                  >
                    <div className="flex items-center justify-between mb-1">
                      <span>{player.name}</span>
                      <span className="text-sm text-gray-400">Lvl {player.level}</span>
                    </div>
                    <div className="h-1 w-full bg-gray-700 rounded-full">
                      <div
                        className="h-full bg-red-600 rounded-full transition-all duration-500"
                        style={{ width: `${(player.wanted_level / 5) * 100}%` }}
                      ></div>
                    </div>
                  </li>
                ))
              )}
            </ul>
          </div>

          <div className="bg-gray-800/90 rounded-xl shadow-2xl p-6 backdrop-blur-sm border border-red-600/20">
            <h2 className="text-xl font-bold text-red-600 mb-4 border-b border-red-600/20 pb-2 flex items-center">
              <span className="flex-1">Crime Ticker</span>
              <span className="text-xs text-gray-400">Live</span>
            </h2>
            <ul className="space-y-3">
              {crimeEvents.length === 0 ? (
                <li className="text-gray-400 italic">No recent criminal activity.</li>
              ) : (
                crimeEvents.map((event, index) => (
                  <li
                    key={index}
                    className="bg-gray-900/60 p-3 rounded-lg text-sm"
                  >
                    <div className="flex items-start justify-between gap-2">
                      <div className="flex-1">
                        <p className="text-red-400 font-bold mb-1">{event.crime}</p>
                        <p className="text-gray-400">Location: {event.location}</p>
                        <p className="text-gray-500">By: {event.perpetratorName}</p>
                      </div>
                      <div className="flex flex-col items-end">
                        <div className="flex gap-1">
                          {Array.from({ length: event.severity }).map((_, i) => (
                            <span key={i} className="w-2 h-2 bg-red-600 rounded-full"></span>
                          ))}
                        </div>
                        <span className="text-xs text-gray-500 mt-1">
                          {new Date(event.timestamp).toLocaleTimeString()}
                        </span>
                      </div>
                    </div>
                  </li>
                ))
              )}
            </ul>
          </div>
        </div>
        <section className="flex-1 relative">
          <div className="absolute inset-0 bg-red-600 opacity-5 pattern-grid"></div>
          <div className="relative bg-gray-800/90 rounded-xl shadow-2xl p-8 backdrop-blur-sm border border-red-600/20">
            <div
              ref={chatWindowRef}
              className="flex flex-col chat-scroll h-[60vh] overflow-y-auto p-6 bg-gray-900/80 rounded-lg mb-6 space-y-4"
              aria-live="polite"
              aria-atomic="false"
            >
              {messages.map((msg, idx) => (
                <div
                  key={idx}
                  className={`max-w-xl break-words p-4 rounded-lg ${
                    msg.role === "user"
                      ? "message-user ml-auto"
                      : msg.role === "assistant"
                      ? "message-assistant mr-auto"
                      : "text-gray-400 italic text-center mx-auto bg-gray-800/50 px-6 py-3"
                  }`}
                >
                  {msg.content}
                </div>
              ))}
              {loading && (
                <div className="text-red-400 italic loading-dots self-start bg-gray-800/50 px-6 py-3 rounded-lg">
                  Processing...
                </div>
              )}
              {showCrimeMenu && (
                <div className="bg-gray-800/90 p-4 rounded-lg border border-red-600/20">
                  <h3 className="text-red-500 font-bold mb-3">Available Jobs:</h3>
                  <div className="grid grid-cols-1 gap-2">
                    {CRIMES.map((crime, index) => (
                      <button
                        key={index}
                        onClick={() => handleCrimeClick(crime)}
                        className="text-left p-3 bg-gray-900/60 rounded-lg hover:bg-gray-900 transition-colors flex justify-between items-center"
                      >
                        <div>
                          <span className="text-red-400">{crime.name}</span>
                          <span className="text-gray-400 text-sm ml-2">{crime.reward}</span>
                        </div>
                        <span className="text-red-500">
                          {"★".repeat(crime.severity)}
                        </span>
                      </button>
                    ))}
                  </div>
                </div>
              )}
            </div>
            <form onSubmit={handleSubmit} className="flex space-x-4">
              <input
                type="text"
                aria-label="Message input"
                aria-required="true"
                autoComplete="off"
                placeholder="Type !help or -h for commands..."
                className="flex-grow rounded-lg bg-gray-700/90 text-gray-100 px-6 py-4 focus:outline-none input-glow border border-red-600/20"
                value={input}
                onChange={(e) => setInput(e.target.value)}
                disabled={loading}
              />
              <button
                type="submit"
                aria-label="Send message"
                className="bg-red-600 text-white rounded-lg px-8 py-4 font-semibold transition button-glow disabled:opacity-50 hover:bg-red-700"
                disabled={loading}
              >
                Send
              </button>
            </form>
          </div>
        </section>
      </main>
      <footer className="bg-gray-900/80 border-t border-red-600/20 p-4 text-center text-gray-500 text-sm backdrop-blur-sm">
        © 2024 Sin City RPG Bot. All rights reserved.
      </footer>
    </div>
  );
}
