import { PlayerData } from './game-data';

type GameUpdate = {
  type: 'player_join' | 'player_leave' | 'player_update' | 'chat_message' | 'initial_state' | 'crime_event' | 'wanted_level_update';
  data: any;
};

export type CrimeEvent = {
  crime: string;
  location: string;
  severity: number;
  timestamp: string;
  perpetrator: string;
  perpetratorName: string;
};

class GameSocket {
  private socket: WebSocket | null = null;
  private listeners: ((update: GameUpdate) => void)[] = [];

  connect() {
    // Connect to our local WebSocket server
    this.socket = new WebSocket('ws://localhost:8001');

    this.socket.onopen = () => {
      console.log('Connected to WebSocket server');
    };

    this.socket.onmessage = (event) => {
      try {
        const update: GameUpdate = JSON.parse(event.data);
        console.log('Received update:', update);
        this.listeners.forEach(listener => listener(update));
      } catch (error) {
        console.error('Error processing message:', error);
      }
    };

    this.socket.onclose = () => {
      console.log('WebSocket connection closed');
      // Attempt to reconnect after a delay
      setTimeout(() => this.connect(), 5000);
    };

    this.socket.onerror = (error) => {
      console.error('WebSocket error:', error);
    };
  }

  subscribe(callback: (update: GameUpdate) => void) {
    this.listeners.push(callback);
    return () => {
      this.listeners = this.listeners.filter(listener => listener !== callback);
    };
  }

  updatePlayer(playerData: PlayerData) {
    if (this.socket?.readyState === WebSocket.OPEN) {
      this.socket.send(JSON.stringify({
        type: 'player_update',
        data: {
          name: playerData.role,
          level: playerData.level,
          inventory: playerData.inventory,
          wanted_level: playerData.wanted_level
        }
      }));
    }
  }

  sendChatMessage(message: string, playerData: PlayerData) {
    if (this.socket?.readyState === WebSocket.OPEN) {
      this.socket.send(JSON.stringify({
        type: 'chat_message',
        data: {
          message,
          player: {
            name: playerData.role,
            level: playerData.level
          }
        }
      }));
    }
  }

  commitCrime(crime: string, playerData: PlayerData) {
    if (this.socket?.readyState === WebSocket.OPEN) {
      this.socket.send(JSON.stringify({
        type: 'commit_crime',
        data: {
          crime,
          location: ["Neon District", "Underground Markets", "Corporate Zone", "Tech Alley"][Math.floor(Math.random() * 4)],
          playerName: playerData.role,
          currentWantedLevel: playerData.wanted_level
        }
      }));
    }
  }
}

export const gameSocket = new GameSocket();
