export const CUSTOM_COMMANDS = {
  // Basic commands
  "!": () => "[ERR] Invalid command",
  "?": () => "[HELP] Type !help for commands",
  ".": () => "[SYS] Online",
  
  // Alternative command formats
  "-h": () => "[HELP] Type !help for commands",
  "-s": (player: PlayerData) => `[STATUS] LVL ${player.level}`,
  "-i": (player: PlayerData) => player.inventory.length > 0 ? `[GEAR] ${player.inventory.join(", ")}` : "[EMPTY]",
  "-c": () => "[CRIME] Type !crime to view jobs",
  "-m": () => "[MARKET] Type !shop to view items",
  
  // Shorthand commands
  "h": () => "[HELP] Type !help for commands",
  "s": (player: PlayerData) => `[STATUS] LVL ${player.level}`,
  "i": (player: PlayerData) => player.inventory.length > 0 ? `[GEAR] ${player.inventory.join(", ")}` : "[EMPTY]",
  "c": () => "[CRIME] Type !crime to view jobs",
  "m": () => "[MARKET] Type !shop to view items",
};

